@media screen and (min-width: 1200px){
    .tripleColumnContainer{
        width: 90%;
        max-width: 90vw;
        
        height: 100vh;
        display: grid;
          grid-template-columns: [start-c] 5fr [center] 3fr [end-c];
          /* grid-template-columns: [start-r] 1fr [center] 1fr [end-r]; */
    
          /* grid-template-columns: 1fr 1fr; */
    
          grid-template-areas: 
          'video    notes'
          'playlist notes'
          ;
    }

    .videoPlayer{
        grid-area:video;
    }
    .playList{
        grid-area:playlist;
    }
    .notes{
        grid-area:notes;
    }


    
}

