  ul {
    overflow: auto;
    height: 80vh;
    padding: 0;
  }

  .centerListItem {
    color: red;
  }


  .ListItem {
    height: fit-content;
    min-height: 20vh;
    /* width: 100%; */
    width: calc(100% -  (1px * 2 ));
    border: 1px solid black;
    /* word-wrap: normal; */
    word-wrap: break-word;
    /*  <pre> tag defines preformatted text. 
        It is used to display code blocks since it preserves spaces and line breaks.
        If the line is large, then the <pre> tag wonâ€™t wrap it by default. 
        To wrap it, we need to use CSS.
        Source: https://www.tutorialspoint.com/How-do-I-wrap-text-in-a-pre-tag-in-HTML    */
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    text-align: start;
    display: flex;
    flex-direction: column;
  }
  .ListItem button{
      width: fit-content;
  }

  @media print {
    ul {
        overflow-y: visible;
        height: fit-content;
        display: block;
        position: relative;
        top:0;

        /* height: 80vh; */
        /* padding: 0; */
      }
    .ListItem {
        height: fit-content;
        min-height: 20vh;
        width: 95vw;
        /* width: 100%; */
        border: 1px solid black;
        margin: 1rem auto;
        border-radius: 10px;
        display: block;
        
      }
  }


  .noteInputField{
    /* white-space: pre-line;  */
    /* whitespace trimmed to single whitespace */
    white-space: pre-wrap;
    /* all whitespace preserved */
  }


  .noteTitleContainer{
      color: rgb(87, 178, 206);
  }
  .noteTitleContainer .noteTitle{
    max-width: 60%;
    /* max-width: 20vw;  */
    /* I will make this explicit for now so that the div doesn't stretch too large. This must change when the note section gets a determined width */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    /* text-align: center; */
    display: inline-block;
    vertical-align: middle;

  }
  .noteTitleContainer .timestamp{
    /* max-width: 70%; */
    width: fit-content;
    /* text-align: center; */
    display: inline-block;
    vertical-align: middle;

  }
  .noteTitleContainer:hover{
      color: blue;
  }


  .noteContent{
    resize: none;
    height: fit-content;
    
    width: calc(100% - (1.5rem * 2) - (1px * 2 ));
    margin: 0;
    flex: 1;
    font-size: 1.2rem;
    padding: .5rem 1.5rem;
    overflow-y: visible;
  }